.checkbox, .simple_checkbox {
    display: flex;
    align-items: center;
    padding: 2px 0;
}
.indicator {
    width: 16px;
    height: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* border: 1px solid #dedede; */
    border-radius: 3px;
    background: #88888817;
    transition: background-color .27s ease;
    
}
.indicator:before, .indicator:after {    
    content: "";
    opacity: 0;
    margin: 0;
    border: 0;
    position: absolute;
    -webkit-transition: opacity 17s ease;
    transition: opacity .17s ease;
}
.checkbox > .indicator:after {
    width: 10px;
    height: 2px;
    background-color: #fff;
}
.checkbox > .indicator:before {
    width: 2px;
    height: 10px;
    background-color: #fff;
}
.checkbox > .filter_value[value~="1"]~.indicator:after, .checkbox > .filter_value[value~="2"]~.indicator:after {
    opacity: 1;
}
.filter_value[value~="1"]~.indicator:before {
    opacity: 1;
}
.checkbox > .filter_value[value~="1"]~.indicator {
    background: #3cce7b;
    border-color: #3cce7b;
}
.checkbox > .filter_value[value~="2"]~.indicator {
    background: #f44336;
    border-color: #f44336;
}



.simple_checkbox> .filter_value[value~="1"]~.indicator {
    background: #00bcd4;
    border-color: #00bcd4;
}
.simple_checkbox>.indicator:before {    
    border-bottom: 2px solid #fff;
    border-right: 2px solid #fff;
    transform: rotate(45deg) translate(-21%, -15%);
    width: 4px;
    height: 8px;
}
